import styled from 'styled-components';

export const AppBarHeader = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 900px;

  background: #ecf1f3;
  margin-top: 32px;
  margin-bottom: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 16px 32px;

  border-radius: 6px;
  box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.3);
`;
